.slide {
  display: flex;
  position: relative;
  

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    filter: brightness(52.5%);
    background-size: cover;
    background-position: center;
    z-index: -1;
  }

  &__content {
    @include sm(margin, auto auto);
    @include sm(padding, 0 2rem);
    margin: auto 0 auto 16.25%;
  }

  &__text {
    &--bold {
      font-weight: 600;
    }

    &--italic-bold {
      font-weight: 600;
      font-style: italic;
    }
  }

  &__decision {
    text-transform: uppercase;
    font-size: 1.2rem;
    font-weight: 600; 
    letter-spacing: 1px;

    position: absolute;
    bottom: 10rem;
    left: 16.25%;
    @include sm(bottom, 4rem);
    @include sm(left, 2rem);
  }
}