.status-menu {
  @include sm(display, none);
  position: fixed;
  bottom: 10rem;
  right: 5.7rem;
  z-index: 250;
  &__elem {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: .6rem 0 .3rem 1rem;
  }
  &__icon {
    height: 2px;
    width: 7rem;
    background-color: $color-white;
    transition: padding-left .4s;
  }
  &__text {
    display: none;
    font-style: italic;
    white-space: nowrap;
  }
  &__elem--active {
    transition: all .3s;
    .status-menu__icon {
      padding-left: 2rem;
    }
    .status-menu__text {
      position: absolute;
      right: 10.7rem;
      display: unset;
    }
  }
}