.controls {
  @include sm(display, none);
  position: fixed;
  top: 50%;
  left: 16.25%;
  transform: translate(-200%, -90%);
  z-index: 250;

  &__arrow {
    width: 2rem;
    display: block;
    cursor: pointer;
    padding: 1rem;
    &--up {
      transform: rotateZ(-90deg);
    }
    &--down {
      transform: rotateZ(90deg);
    }
  }
}