.slide-advantages {
  &::before {
    background-image: url(../img/advantages-bg.jpg);
  }

  &__content {
    @include sm(padding-right, 0 !important);
    @include sm(padding-left, 2rem !important);
    @include sm(overflow, hidden);
    display: flex;
    flex-direction: column;
    max-width: 96rem;
  }

  &__slider {
    @include sm(margin-left, 0);
    width: 100%;
    margin-left: -2rem;
  }

  &__slider-wrapper {
    display: flex;
  }

  &__advantage {
    @include sm(width, 60%);
    @include sm(box-sizing, unset);
    padding: 3.5rem 2rem;
    width: calc(33% - 1.6rem);
    transition: all .3s ease;
    flex-shrink: 0;
    box-sizing: border-box;

    &:nth-child(2) {
      margin: 0 2.4rem; 
      @include sm(margin, 0);
    }

    &:nth-child(3) {
      @include sm(width, calc(91% - 2rem));
      @include sm(margin-right, 2.4rem !important);
    }

    &:hover {
      background-color: rgba(0,0,0,.5);
    }
  }

  &__panel {
    width: calc(100% + 2rem);
    margin: 0 0 .5rem -2rem;
  }

  &__panel-elem {
    width: calc(100% - 2rem) !important;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-bottom: 5rem;
    padding-left: 4rem;
    
    &:nth-child(1) {
      & .slide-advantages__panel-line {
        left: 6rem;
      }
    } 

    &:nth-child(3) {
      margin-right: 2rem;
      & .slide-advantages__panel-img {
        transform: rotateZ(180deg);
        padding-right: 0;
        padding-left: 4rem;
      }
      & .slide-advantages__panel-line {
        left: unset;
        right: 8rem;
      }
    }
  }

  &__panel-text {
    text-transform: uppercase;
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 1px;
  }

  &__panel-img {
    height: 2rem;
    padding-right: 4rem;
  }

  &__panel-line {
    width: 100%;
    height: 1px;
    background-color: $color-white;
    position: absolute;
    left: 0;
    bottom: 1px;
  }

  &__heading {
    font-size: 2.8rem;
    font-style: italic;
    margin-bottom: 2.8rem;
  }
}