.portfolio {
  display: grid;
  height: 100%;
  grid-template-columns: 1fr 1fr 1fr 5.5rem;
  grid-template-rows: 23vh 23vh;
  gap: 1.2rem;

  &__example {
    background-color: $color-grey;
    border-bottom: 3px solid $color-grey;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    overflow: hidden;

    &::before{
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 0;
      background-image: linear-gradient(180deg, transparent 25%, $color-black 100%);
      transition: all 0.4s ease-in-out;
    }
    
    &:hover {
      &::before {
        height: 100%;
      }

      .portfolio__text {
        bottom: 25px;
      }
    }
  }

  &__text {
    z-index: 250;
    position: absolute;
    bottom: -20px;
    transition: bottom 0.4s ease-in-out;
    font-size: 1.6rem;
  }

  &__btn {
    grid-row: 1 / 3;
    grid-column: 4 / 5;
    position: relative;
    background-color: $color-btn-1;
    cursor: pointer;
    transition: all .3s ease;

    &:hover {
      box-shadow:0 0 20px rgba($color-btn-2,.35);
    }
  }

  &__btn-text {
    position: absolute;
    top: 50%;
    left: 50%;
    text-transform: uppercase;
    word-spacing: 150%;
    transform-origin: 0 0;
    transform: rotateZ(90deg) translateX(-50%) translateY(calc(50% - 19.6px));
    width: 13rem;
    font-weight: 600;
  }
}