.navigation {
  @include sm(right, 2rem);
  @include sm(top, 2rem);
  position: fixed;
  right: 5.7rem;
  top: 5rem;
  z-index: 250;
  &__checkbox,
  &__check-list {
      display: none;
  }

  &__button {
    @include sm(height, 2.2rem);
    display: flex;
    height: 2rem;
    z-index: 2000;
    cursor: pointer;
    position: relative;
  }

  &__text {
    @include sm(display, none);
    font-size: 1.6rem;
    text-transform: uppercase;
    margin-right: .75rem;
  }

  &__nav {
    display: none;
    position: absolute;
    z-index: 1500;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: -30.6px;
    background-color: rgba($color-black, .8);
  }

  &__content {
    margin-top: 113.6px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
    width: 85%;
    margin-left: auto;
  }

  &__links-list {
      height: 0;
      transition: all .6s;
      overflow: hidden;
  }

  &__list-elem {
      text-align: right;
      margin-right: 4rem;
      font-size: 1.3rem;
      line-height: 1.2;
  }

  &__link {
    display: block;
    padding: 1.25rem 0;
      &:link,
      &:visited {
          color: $color-white;
      }
      &--active {
        color: $text-color !important;
      }
  }

  &__link--main {
    margin-right: 2rem;
    padding: 0;
  }

  &__block-header {
    margin-right: 4.5rem;
    padding: 3rem 0 1.5rem;
    position: relative;
  }



  &__block-header,
  &__link--main:link,
  &__link--main:visited {
    text-transform: uppercase;
    color: $text-color;
    font-weight: 700;
    font-size: 1.7rem;
    text-align: right;
    cursor: pointer;
  }

  &__link:hover,
  &__block-header:hover {
    color: $text-color;
  }

  &__arrow-down {
    display: none;
  }

  &__arrow-down,
  &__arrow-left {
    @include from(483px) {
      bottom: 15px;
    }
    position: absolute;
    right: -21px;
    bottom: 26px;
    font-size: 1.5rem;
  }

  &__check-list:checked ~ &__block-header>&__arrow-down {
    display: unset;
  }

  &__check-list:checked ~ &__links-list {
    height: 300px;
  }

  &__check-list:checked ~ &__block-header>&__arrow-left {
    display: none;
  }

  //FUNCTIONALITY

  &__checkbox:checked ~ &__nav {
    display: block;
  }


  //ICON
  &__icon {
    @include sm(flex, 0 0 2.5rem);
    position: relative;
    margin-top: 1rem;
    flex: 0 0 2rem;

    &,
    &::before,
    &::after {
      @include sm(width, 2.5rem);
      @include sm(height, 2px);
      width: 2rem;
      height: 1px;
      background-color: $color-white;
      display: inline-block;
      transition: all .2s;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
    }

    &::before {
      @include sm(top, -.7rem);
      top: -.5rem;
    }
    &::after {
      @include sm(top, .7rem);
      top: .5rem;
    }
  }

  &__button:hover &__icon::before {
    @include sm(top, -.8rem);
    top: -.6rem;
  }

  &__button:hover &__icon::after {
    @include sm(top, .8rem);
    top: .6rem;
  }

  &__checkbox:checked + &__button &__icon {
    background-color: transparent;
  }

  &__checkbox:checked + &__button &__icon::before {
    top: 0;
    transform: rotate(135deg);
  }

  &__checkbox:checked + &__button &__icon::after {
    top: 0;
    transform: rotate(-135deg);
  }
  &__checkbox:checked + &__button &__icon::after,
  &__checkbox:checked + &__button &__icon::before,
  &__checkbox:checked + &__button &__icon {
    @include sm(height, 3px);
    border-radius: 3px;
    height: 2px;
  }
}