.slide-contacts {
  &::before {
    background-image: url(../img/contacts-bg.jpg);
  }

  &__content {
    max-width: 85rem;
  }

  &__btn-section {
    @include sm(width, unset);
    @include sm(grid-template-columns, 1fr);
    @include sm(gap, 2rem);
    margin-top: 4rem;
    width: 75%;
    display: grid;
    gap: 2.7rem;
    grid-template-columns: 1fr 1fr 1fr;
  }

  &__btn {
    cursor: pointer;
    padding: .8rem 0;
    text-align: center;
    transition: all .3s ease;
    &:nth-child(1) {
      background-color: $color-btn-1;
      &:hover {
        box-shadow:0 0 20px rgba($color-btn-1,.35);
      }
    }

    &:nth-child(2) {
      background-color: $color-btn-2;
      &:hover {
        box-shadow:0 0 20px rgba($color-btn-2,.35);
      }
    }

    &:nth-child(3) {
      background-color: $color-btn-3;
      &:hover {
        box-shadow:0 0 20px rgba($color-btn-3,.35);
      }
      margin-right: 0;
    }
  }

  &__btn-text {
    font-size: 1.2em;
    font-weight: 600;
  }
}