.slide-form {
  &::before {
    background-image: url(../img/form-bg.jpg);
  }

  &__content {
    max-width: 46.5%;
    @include sm(display, none);
  }

  &__dev {
    display: none;
    @include sm(display, unset);
  }

  &__control-btn {
    display: inline-block;
    font-size: 1.8rem;
    font-weight: 600;
    text-transform: uppercase;
    padding: 0 .3rem .7rem .3rem;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 11rem;
    }

    &--active {
      border-bottom: 3px solid $color-white;
    }
  }

  &__checkbox {
    font-size: 1.6rem;
  }

  &__settings {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 2.2rem;
  }

  &__icon {
    display: inline-block;
    width: 3rem;
    height: 1.6rem;
    border: 1px solid $color-white;
    border-radius: 3.2rem;
    position: relative;
    top: .4rem;
    margin-right: 1.2rem;
    transition: all .3s cubic-bezier(0.6, -0.28, 0.735, 0.045);

    &::before {
      content: "";
      display: block;
      background-color: $color-white;
      width: 1.2rem;
      height: 1.2rem;
      border-radius: 100%;
      position: absolute;
      top: .2rem;
      left: .3rem;
      transition: all .3s cubic-bezier(0.6, -0.28, 0.735, 0.045);
    }
  }

  input[type=checkbox] {
    display: none;
  }

  input[type=checkbox]:checked + &__checkbox &__icon {
    border-color: $color-blue-1;
  }

  input[type=checkbox]:checked + &__checkbox &__icon::before {
    background-color: $color-blue-1;
    left: 1.5rem;
  }

  &__text {
    margin-bottom: 4rem;
  }

  &__controls {
    margin: 0 0 2rem 3rem;
  }

  &__submit {
    background-color: $color-btn-1;
    display: inline-block;
    padding: 1.3rem 2.7rem;
    font-size: 1.3rem;
    font-weight: 600;
    text-transform: uppercase;
    cursor: pointer;
    margin-top: 4.5rem;
    transition: all .3s ease;
    &:hover {
      box-shadow:0 0 20px rgba($color-btn-2,.35);
    }

  }

  &__price {
    font-size: 2rem;
    margin-right: 4rem;
    display: inline-block;
    font-style: italic;
    margin-top: 5rem;
  }

  &__time {
    font-size: 1.6rem;
    display: inline-block;
  }

  &__chipper {
    font-size: 1.2rem;
    margin-top: 2rem;
  }

}