.social {
  @include sm(right, 6.5rem);
  @include sm(top, 2.4rem);
  @include sm(bottom, unset);
  @include sm(left, unset);
  position: fixed;
  bottom: 5rem;
  left: 5.7rem;
  display: flex;
  align-items: center;
  z-index: 250;

  &__link:last-child {
    margin-left: 2rem;
  }

  &__image {
    @include sm(height, 1.4rem);
    display: block;
    height: 1.6rem;
  }
}