.slide-welcome {
  &::before {
    background-image: url(../img/welcome-bg.jpg);
    filter: brightness(67.5%) !important;
  }

  &__content {
    @include sm(width, 35rem);
    @include sm(transform, translateY(-5rem));
  }

  &__name {
    @include sm(display, none);
    font-size: $font-size-h3;
    padding-bottom: .4rem;
  }

  &__heading {
    @include sm(font-size, $font-size-h1-mobile);
    font-size: $font-size-h1;
    line-height: 1.2;
    font-weight: 600;
  }

  &__br {
    @include sm(display, none);
  }
}